"use client";

import { useState } from "react";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Checkbox } from "@/components/ui/checkbox";
import { Input } from "@/components/ui/input";
import { Textarea } from "@/components/ui/textarea";
import { Dialog, DialogContent, DialogDescription, DialogHeader, DialogTitle, DialogTrigger } from "@/components/ui/dialog";
import { Label } from "@/components/ui/label";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";

// Mock data for tasks
const allTasks = [
  {
    id: 1,
    title: "Design new landing page",
    description: "Create mockups for the new marketing landing page",
    status: "todo",
    priority: "High",
    assignee: "Mike Johnson",
    project: "Website Redesign",
    dueDate: "2024-07-25",
    createdDate: "2024-07-15"
  },
  {
    id: 2,
    title: "Fix navigation bug",
    description: "Users report navigation menu not working on mobile",
    status: "in-progress",
    priority: "Medium",
    assignee: "Sarah Wilson",
    project: "Mobile App",
    dueDate: "2024-07-22",
    createdDate: "2024-07-14"
  },
  {
    id: 3,
    title: "Update API documentation",
    description: "Document new endpoints for the user management API",
    status: "done",
    priority: "Low",
    assignee: "Alex Brown",
    project: "Backend Services",
    dueDate: "2024-07-20",
    createdDate: "2024-07-10"
  },
  {
    id: 4,
    title: "Implement user authentication",
    description: "Add OAuth2 authentication for Google and GitHub",
    status: "in-progress",
    priority: "High",
    assignee: "Jane Smith",
    project: "User Management",
    dueDate: "2024-07-28",
    createdDate: "2024-07-12"
  },
  {
    id: 5,
    title: "Create marketing materials",
    description: "Design brochures and social media posts for Q3 campaign",
    status: "todo",
    priority: "Medium",
    assignee: "Lisa Anderson",
    project: "Marketing Campaign",
    dueDate: "2024-07-30",
    createdDate: "2024-07-16"
  },
  {
    id: 6,
    title: "Database optimization",
    description: "Optimize slow queries in the user dashboard",
    status: "todo",
    priority: "High",
    assignee: "Chris Miller",
    project: "Backend Services",
    dueDate: "2024-07-26",
    createdDate: "2024-07-13"
  }
];

export default function TasksPage() {
  const [tasks, setTasks] = useState(allTasks);
  const [showNewTaskDialog, setShowNewTaskDialog] = useState(false);
  const [newTask, setNewTask] = useState({
    title: "",
    description: "",
    priority: "Medium",
    assignee: "",
    project: "",
    dueDate: "",
  });

  const handleCreateTask = () => {
    const task = {
      id: tasks.length + 1,
      title: newTask.title,
      description: newTask.description,
      status: "todo",
      priority: newTask.priority,
      assignee: newTask.assignee,
      project: newTask.project,
      dueDate: newTask.dueDate,
      createdDate: new Date().toISOString().split('T')[0],
    };
    setTasks([task, ...tasks]);
    setShowNewTaskDialog(false);
    setNewTask({ title: "", description: "", priority: "Medium", assignee: "", project: "", dueDate: "" });
  };

  const updateTaskStatus = (taskId: number, newStatus: string) => {
    setTasks(tasks.map(task => 
      task.id === taskId ? { ...task, status: newStatus } : task
    ));
  };

  const getStatusBadge = (status: string) => {
    const colors = {
      todo: "bg-gray-100 text-gray-800",
      "in-progress": "bg-blue-100 text-blue-800",
      done: "bg-green-100 text-green-800",
    };
    return (
      <Badge className={colors[status as keyof typeof colors] || "bg-gray-100 text-gray-800"}>
        {status.replace('-', ' ').toUpperCase()}
      </Badge>
    );
  };

  const getPriorityBadge = (priority: string) => {
    const colors = {
      High: "bg-red-100 text-red-800",
      Medium: "bg-yellow-100 text-yellow-800",
      Low: "bg-blue-100 text-blue-800",
    };
    return (
      <Badge className={colors[priority as keyof typeof colors] || "bg-gray-100 text-gray-800"}>
        {priority}
      </Badge>
    );
  };

  const TaskList = ({ status }: { status: string }) => {
    const filteredTasks = tasks.filter(task => task.status === status);
    
    return (
      <div className="space-y-4">
        {filteredTasks.map((task) => (
          <Card key={task.id} className="hover:shadow-md transition-shadow">
            <CardContent className="p-4">
              <div className="flex items-start justify-between">
                <div className="flex-1">
                  <h3 className="font-semibold text-lg mb-1">{task.title}</h3>
                  <p className="text-gray-600 text-sm mb-2">{task.description}</p>
                  <div className="flex items-center space-x-4 text-sm text-gray-500">
                    <span>Project: {task.project}</span>
                    <span>Assignee: {task.assignee}</span>
                    <span>Due: {task.dueDate}</span>
                  </div>
                </div>
                <div className="flex items-center space-x-2">
                  {getPriorityBadge(task.priority)}
                  <Select 
                    value={task.status} 
                    onValueChange={(value) => updateTaskStatus(task.id, value)}
                  >
                    <SelectTrigger className="w-32">
                      <SelectValue />
                    </SelectTrigger>
                    <SelectContent>
                      <SelectItem value="todo">To Do</SelectItem>
                      <SelectItem value="in-progress">In Progress</SelectItem>
                      <SelectItem value="done">Done</SelectItem>
                    </SelectContent>
                  </Select>
                </div>
              </div>
            </CardContent>
          </Card>
        ))}
      </div>
    );
  };

  return (
    <div className="flex-1 overflow-auto bg-gray-50">
      <div className="p-6">
        {/* Header */}
        <div className="flex items-center justify-between mb-6">
          <div>
            <h1 className="text-3xl font-bold text-gray-900">Tasks</h1>
            <p className="text-gray-600 mt-1">Manage and track all your project tasks</p>
          </div>
          <Dialog open={showNewTaskDialog} onOpenChange={setShowNewTaskDialog}>
            <DialogTrigger asChild>
              <Button className="bg-purple-600 hover:bg-purple-700">
                + Create New Task
              </Button>
            </DialogTrigger>
            <DialogContent className="sm:max-w-[425px]">
              <DialogHeader>
                <DialogTitle>Create New Task</DialogTitle>
                <DialogDescription>
                  Add a new task to your project. Fill in the details below.
                </DialogDescription>
              </DialogHeader>
              <div className="grid gap-4 py-4">
                <div className="grid gap-2">
                  <Label htmlFor="title">Task Title</Label>
                  <Input
                    id="title"
                    value={newTask.title}
                    onChange={(e) => setNewTask({ ...newTask, title: e.target.value })}
                    placeholder="Enter task title"
                  />
                </div>
                <div className="grid gap-2">
                  <Label htmlFor="description">Description</Label>
                  <Textarea
                    id="description"
                    value={newTask.description}
                    onChange={(e) => setNewTask({ ...newTask, description: e.target.value })}
                    placeholder="Enter task description"
                  />
                </div>
                <div className="grid gap-2">
                  <Label htmlFor="priority">Priority</Label>
                  <Select 
                    value={newTask.priority} 
                    onValueChange={(value) => setNewTask({ ...newTask, priority: value })}
                  >
                    <SelectTrigger>
                      <SelectValue placeholder="Select priority" />
                    </SelectTrigger>
                    <SelectContent>
                      <SelectItem value="High">High</SelectItem>
                      <SelectItem value="Medium">Medium</SelectItem>
                      <SelectItem value="Low">Low</SelectItem>
                    </SelectContent>
                  </Select>
                </div>
                <div className="grid gap-2">
                  <Label htmlFor="assignee">Assignee</Label>
                  <Input
                    id="assignee"
                    value={newTask.assignee}
                    onChange={(e) => setNewTask({ ...newTask, assignee: e.target.value })}
                    placeholder="Enter assignee name"
                  />
                </div>
                <div className="grid gap-2">
                  <Label htmlFor="project">Project</Label>
                  <Input
                    id="project"
                    value={newTask.project}
                    onChange={(e) => setNewTask({ ...newTask, project: e.target.value })}
                    placeholder="Enter project name"
                  />
                </div>
                <div className="grid gap-2">
                  <Label htmlFor="dueDate">Due Date</Label>
                  <Input
                    id="dueDate"
                    type="date"
                    value={newTask.dueDate}
                    onChange={(e) => setNewTask({ ...newTask, dueDate: e.target.value })}
                  />
                </div>
              </div>
              <div className="flex justify-end space-x-2">
                <Button variant="outline" onClick={() => setShowNewTaskDialog(false)}>
                  Cancel
                </Button>
                <Button onClick={handleCreateTask} className="bg-purple-600 hover:bg-purple-700">
                  Create Task
                </Button>
              </div>
            </DialogContent>
          </Dialog>
        </div>

        {/* Task Stats */}
        <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-6">
          <Card>
            <CardHeader className="pb-3">
              <CardTitle className="text-sm font-medium text-gray-600">To Do</CardTitle>
              <CardDescription className="text-2xl font-bold text-gray-900">
                {tasks.filter(t => t.status === "todo").length}
              </CardDescription>
            </CardHeader>
          </Card>
          <Card>
            <CardHeader className="pb-3">
              <CardTitle className="text-sm font-medium text-gray-600">In Progress</CardTitle>
              <CardDescription className="text-2xl font-bold text-gray-900">
                {tasks.filter(t => t.status === "in-progress").length}
              </CardDescription>
            </CardHeader>
          </Card>
          <Card>
            <CardHeader className="pb-3">
              <CardTitle className="text-sm font-medium text-gray-600">Completed</CardTitle>
              <CardDescription className="text-2xl font-bold text-gray-900">
                {tasks.filter(t => t.status === "done").length}
              </CardDescription>
            </CardHeader>
          </Card>
        </div>

        {/* Task Tabs */}
        <Tabs defaultValue="todo" className="w-full">
          <TabsList className="grid w-full grid-cols-3">
            <TabsTrigger value="todo">To Do ({tasks.filter(t => t.status === "todo").length})</TabsTrigger>
            <TabsTrigger value="in-progress">In Progress ({tasks.filter(t => t.status === "in-progress").length})</TabsTrigger>
            <TabsTrigger value="done">Done ({tasks.filter(t => t.status === "done").length})</TabsTrigger>
          </TabsList>
          <TabsContent value="todo">
            <TaskList status="todo" />
          </TabsContent>
          <TabsContent value="in-progress">
            <TaskList status="in-progress" />
          </TabsContent>
          <TabsContent value="done">
            <TaskList status="done" />
          </TabsContent>
        </Tabs>
      </div>
    </div>
  );
}
